import styles from './VideoItem.module.scss';
import classNames from 'classnames/bind';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart, faCommentDots, faShare, faAngleDown } from '@fortawesome/free-solid-svg-icons';
import Button from '../Button';
import {
    CodeIcon,
    EmailIcon,
    FacebookIcon,
    LineIcon,
    LinkedInIcon,
    LinkIcon,
    PinterestIcon,
    RedditIcon,
    ShareFlyIcon,
    TelegramIcon,
    TwitterIcon,
    WhatsAppIcon,
} from '../Icons';
import Tippy from '@tippyjs/react/headless';
import { Wrapper as PopperWrapper } from '~/components/Popper';
import { useState } from 'react';

const items = [
    {
        title: 'Nhng',
        icon: <CodeIcon />,
    },
    {
        title: 'Gi n bn b',
        icon: <ShareFlyIcon />,
    },
    {
        title: 'Chia s vi Facebook',
        icon: <FacebookIcon />,
    },
    {
        title: 'Chia s vi WhatsApp',
        icon: <WhatsAppIcon />,
    },
    {
        title: 'Sao chp lin kt',
        icon: <LinkIcon />,
    },
    {
        title: 'Chia s vi Twitter',
        icon: <TwitterIcon />,
    },
    {
        title: 'Chia s vi LinkedIn',
        icon: <LinkedInIcon />,
    },
    {
        title: 'Chia s vi Reddit',
        icon: <RedditIcon />,
    },
    {
        title: 'Chia s vi Telegram',
        icon: <TelegramIcon />,
    },
    {
        title: 'Chia s vi Email',
        icon: <EmailIcon />,
    },
    {
        title: 'Chia s vi Line',
        icon: <LineIcon />,
    },
    {
        title: 'Chia s vi Pinterest',
        icon: <PinterestIcon />,
    },
];
const cx = classNames.bind(styles);
function Action() {
    const [listItems, setListItems] = useState(items.slice(0, 5));
    const [isMore, setIsMore] = useState(true);

    const handleShareMore = () => {
        setListItems(items);
        setIsMore(false);
    };

    const handleShareMoreHide = () => {
        setListItems(items.slice(0, 5));
        setIsMore(true);
    };
    const renderPreview = (props) => {
        return (
            <div className={cx('share-list')} tabIndex="-1" {...props}>
                <PopperWrapper className={cx('wrapper-list')}>
                    {listItems.map((item, index) => (
                        <div key={index} className={cx('item')}>
                            <span className={cx('icon')}>{item.icon}</span>
                            <span className={cx('title')}>{item.title}</span>
                        </div>
                    ))}
                    {isMore && (
                        <div className={cx('share-more')} onClick={handleShareMore}>
                            <FontAwesomeIcon icon={faAngleDown} />
                        </div>
                    )}
                </PopperWrapper>
            </div>
        );
    };

    return (
        <div className={cx('action')}>
            <div className={cx('like-action')}>
                <Button className={cx('like-icon')}>
                    <FontAwesomeIcon icon={faHeart} />
                </Button>
                <div className={cx('count')}>123</div>
            </div>
            <div className={cx('comment-action')}>
                <Button className={cx('comment-icon')}>
                    <FontAwesomeIcon icon={faCommentDots} />
                </Button>
                <div className={cx('count')}>123k</div>
            </div>
            <Tippy
                interactive
                delay={[0, 300]}
                offset={[-24, 11]}
                placement="top-start"
                render={renderPreview}
                onHide={handleShareMoreHide}
            >
                <div className={cx('share-action')}>
                    <Button className={cx('share-icon')}>
                        <FontAwesomeIcon icon={faShare} />
                    </Button>
                    <div className={cx('count')}>123</div>
                </div>
            </Tippy>
        </div>
    );
}

export default Action;
